import React from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom'
import { displayNotificationForSeconds } from '../reducers/notificationReducer'
import { initializeAllProducts } from '../reducers/productReducer'
import customerService from '../services/customers'

// Material UI:
import { makeStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
}))

const LoginPage = () => {
  const classes = useStyles()

  const dispatch = useDispatch()
  const history = useHistory()

  const [email, setEmail] = React.useState('')
  const [password, setPassword] = React.useState('')
  const [disabled, setDisabled] = React.useState(false)

  const handleEmailChange = (event) => {
    setEmail(event.target.value)
  }
  const handlePasswordChange = (event) => {
    setPassword(event.target.value)
  }

  const handleLogIn = async () => {
    setDisabled(true)

    try{
      const tokenAndInfo = await customerService.postLogin({
        email,
        password
      })
      dispatch({
        type: 'ADD_LOGGED_IN',
        data: tokenAndInfo
      })

      if(tokenAndInfo.admin !== undefined) dispatch(initializeAllProducts(tokenAndInfo.token))

      window.localStorage.setItem('genericStoreUser', JSON.stringify(tokenAndInfo))

      history.push('/')

      dispatch(displayNotificationForSeconds('Tervetuloa!', 'success', 5))
    }
    catch(error) {
      if (error.response.data.error.includes('Incorrect email or password')) dispatch(displayNotificationForSeconds('Virheellinen shkpostiosoite tai salasana', 'error', 5))
      else dispatch(displayNotificationForSeconds('Sisnkirjautuminen eponnistui', 'error', 5))
      setDisabled(false)
    }
  }


  // MAIN COMPONENT:

  return (
    <form className={classes.root} noValidate autoComplete="off">
      <div>
        <TextField required id="standard-required" label="Shkposti" value={email} onChange={handleEmailChange} />
        <TextField required id="standard-required" label="Salasana" value={password} onChange={handlePasswordChange} />
        <br />
        <Button size="small" disabled={disabled} onClick={handleLogIn}>Kirjaudu sisn</Button>
        <br />
        <br />
        <Link to="/rekisteroityminen">
          <Button size="small">Rekisteridy</Button>
        </Link>
      </div>
    </form>
  )
}

export default LoginPage